// create a full heading component in chakra ui

import { Heading, Highlight, VStack } from "@chakra-ui/react";

const Header = () => {
    return (
        <VStack textAlign={"center"}>
            <Heading as="h1" fontSize={["7xl", "8xl", "240px"]} noOfLines={1} data-aos="zoom-in">
                IGNITE
            </Heading>
            <Heading as="h2" fontSize={["3xl", "4xl", "5xl"]} noOfLines={2} fontFamily={"'Rock Salt', cursive"}>
                <Highlight query="Spark" styles={{ px: "2", py: "1", rounded: "full", bg: "orange.400" }}>
                    Fueling the Spark of Entrepreneurship
                </Highlight>
            </Heading>
        </VStack>
    );
};

export default Header;
